@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
.home{display:flex;flex-direction:column;margin-top:30px}.home .apartments{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:30px;gap:30px;background-color:#f6f6f6;padding:40px 30px;border-radius:25px;margin-top:20px}
:export{primary-color:#ff6060;font-family:"Montserrat",sans-serif}*{font-family:"Montserrat",sans-serif}body{margin:20px 50px 0 50px}
.banner{position:relative;width:100%;height:223px;border-radius:25px;margin-top:20px}.banner img{display:inline-block;object-fit:cover;border-radius:inherit;width:inherit;height:inherit}.banner .overlay{position:absolute;object-fit:cover;width:100%;inset:0;border-radius:inherit;background:rgba(0,0,0,.3);display:flex;align-items:center;justify-content:center}.banner .overlay p{display:inline-block;color:#fff;font-weight:500;font-size:48px}
.card{position:relative;overflow:hidden;border-radius:10px}.card img{display:block;object-fit:contain;height:340px;border-radius:inherit}.card .overlay{position:absolute;object-fit:contain;border-radius:inherit;width:100%;height:100%;z-index:1;left:0;bottom:0;background:linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(255, 255, 255, 0.2) 50%);display:flex;align-items:flex-end;justify-content:flex-start}.card .overlay p{display:inline-block;margin-left:1em;color:#fff;inset:0;font-weight:500;font-size:18px}
:export{primary-color:#ff6060;font-family:"Montserrat",sans-serif}.error{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:600px}.error *{display:inline-block;color:#ff6060;margin:0}.error .code{font-size:288px;font-weight:700}.error .alert{font-size:36px;font-weight:500}.error a{font-size:18px;font-weight:500}
:export{primary-color:#ff6060;font-family:"Montserrat",sans-serif}.header{display:flex;justify-content:space-between;align-items:center;height:68px}.header img{display:inline-block}.header div a{font-family:"Montserrat";font-weight:500;font-size:24px;color:#ff6060;margin-left:57px}
.footer{display:flex;flex-direction:column;justify-content:center;align-items:center;height:209px;width:100%;margin-top:30px;background-color:#000}.footer img{display:inline-block;height:auto;width:122px}.footer p{font-family:"Montserrat";font-weight:500;font-size:24px;color:#fff;margin:10px 0 0 0}
